<template>
	<b-card :header="$t('Orders')">
		<!-- <b-table :dark="dark" :sort-by.sync="sortBy" :hover="hover" :striped="striped"  :bordered="bordered" :small="small" :fixed="fixed" responsive="lg" :items="gridData" :fields="captions" :current-page="currentPage" :per-page="perPage">
				<template slot="show_details" slot-scope="row">
					<b-button size="sm" @click="row.toggleDetails(), toggleData(row.index)" class="mr-2">
					{{ row.detailsShowing ? '- ' : '+' }}
					</b-button>
				</template>

				<template slot="row-details" slot-scope="row">
					<b-table style="margin-left: 25px;" :dark="dark" :sort-by.sync="sortBy" :hover="hover" :striped="striped"  :bordered="bordered" :small="small" :fixed="fixed" responsive="lg" :items="innerGridData" :fields="innerTableFields" :current-page="currentPage" :per-page="perPage">
					</b-table>
				</template>
		</b-table>
		<div class="pt-1 pb-4" v-if="gridData.length == 0">
		<b-button style="background-color: #e4e5e6" class="w-100">{{$t('No Records Found.')}}</b-button>
		</div>
		<nav>
		<b-pagination class="pull-right" :total-rows="totalRows" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
		</nav> -->
        
		<b-table :dark="dark" :sort-by.sync="sortBy" :hover="hover" :striped="striped"  :bordered="bordered" :small="small" :fixed="fixed" responsive="lg" :items="gridData" :fields="captions" :current-page="currentPage" :per-page="perPage">
          
		   <template slot="top-row" slot-scope="{ fields }" v-if="currentPage==1">
        <td v-for="field in fields" :key="field.key"  >
          <input   v-model="filters[field.key]" :placeholder="' '+field.label" class="form-control" v-if="field.key!='actions' && field.key!='index'" >
        </td>
      </template>
		  
        <template slot="index" slot-scope="data">
        {{ data.index + 1 }}
      </template>		
      <template slot="actions" slot-scope="data">
          <a v-on:click="clearObjects(),selectObject(data.item), $refs.StoreDetailsModel.show()" v-b-tooltip.hover :title="$t('Check_Details')" class="icon mr-2 anchor-pointer"><i id="editIcon" class="fa font-2x2 fa-pencil"></i></a>
      </template>
    </b-table>
        <div class="pt-1 pb-4" v-if="gridData2.length == 0">
        <!-- <b-button style="background-color: #e4e5e6" class="w-100">{{$t('No Records Found.')}}</b-button> -->
		     <div class="text-center">
      <b-spinner variant="primary" label="Text Centered"></b-spinner>
          </div>
        </div>
        <nav>
        <b-pagination class="pull-right" :total-rows="totalRows" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
        </nav>

	<b-modal no-close-on-backdrop ref="StoreDetailsModel" size="lg" hide-footer :title="$t('Order_Details')" class="modal-primary">




     <!-- <b-table :dark="dark" :sort-by.sync="sortBy" :hover="hover" v-model="shownData2" :striped="striped"  :bordered="bordered" :small="small" :fixed="fixed" responsive="lg" :items="addressGridData" :fields="addressTableFields" :current-page="currentPage" :per-page="selectedperPage">
				
	</b-table> -->
      <b-row style="border-bottom:5px solid white">
		 <b-col md="6">
			 <label ><b >{{$t('Order_ID')}}</b></label>
           
			       <!-- <label >{{order_Details_field.OrderNo}}</label> -->
                    <b-input-group>
                      <b-form-input type="text" v-model="order_Details_field.OrderNo"  readonly  ></b-form-input>
                    </b-input-group>
        </b-col>
    
		 <b-col md="6">
			<label ><b >{{$t('Order_Date')}}</b></label>    
       
                    <b-input-group>
                      <b-form-input type="text" v-model="order_Details_field.OrderDate" readonly  ></b-form-input>
                    </b-input-group>
        </b-col>
     </b-row>
	 <b-row style="border-bottom:5px solid white">
		 <b-col md="6">
			 <label ><b >{{$t('Payment_Status')}}</b></label>   
           
                    <b-input-group>
                      <b-form-input type="text" v-model="order_Details_field.PaymentStatus"  readonly ></b-form-input>
                    </b-input-group>
        </b-col>
    
		 <b-col md="6">
			 <label ><b >{{$t('Total_Amount')}}</b></label>   
           
                    <b-input-group>
                      <b-form-input type="text" v-model="order_Details_field.TotalAmount" readonly  ></b-form-input>
                    </b-input-group>
        </b-col>
     </b-row>
	 <b-row style="border-bottom:5px solid white">
		 <b-col md="6">
			<label ><b>{{$t('Order_Delivery_Address')}}</b></label>    
         
                    <b-input-group>
                      <b-form-input type="text" v-model="order_Details_field.OrderDeliveryAddress"  readonly ></b-form-input>
                    </b-input-group>
        </b-col>
     </b-row>
      <br>
       




	    <b-row style="border-bottom:5px solid white">
		 <b-col md="3" style="background-color: #ef402b; border-radius: 25px; font-size: 19px; text-align: center; " >
			 <label style='color:white;'><b>{{$t('Order_Items')}}</b></label>
        </b-col>
        </b-row>
				
       <b-table :dark="dark" :sort-by.sync="sortBy" :hover="hover" v-model="shownData" :striped="striped"  :bordered="bordered" :small="small" :fixed="fixed" responsive="lg" :items="storeGridData" :fields="storecaptions" :current-page="currentPage" :per-page="selectedperPage">
				
				
				<template slot="show_details" slot-scope="row">
					<b-button size="sm" @click="row.toggleDetails(), toggleData(row.index)" class="mr-2">
					{{ row.detailsShowing ? '- ' : '+' }}
					</b-button>
				</template>
					<template slot="status" scope="item"> 
				<b-form-select v-model="item.item.Status" required>   
                  <option v-for="order in orderStatusList" :value="order.StatusId" v-bind:key="order.StatusId">{{order.StatusValue}}</option> 
                </b-form-select>
					</template>
					<template slot="actions" scope="item">
					   <b-btn size="sm" :id="item.item.StoreID" v-if="showLoader!=item.item.StoreID" @click="showLoader=item.item.StoreID,UpdateStatus(item.item)">{{$t('Update')}}</b-btn>
					   <b-button variant="primary" v-if="showLoader==item.item.StoreID"  :id="item.item.StoreID" >
							<b-spinner small></b-spinner>
							<span class="sr-only">Loading...</span>
						</b-button> 
					</template>

				<template slot="row-details" slot-scope="row">
					<b-table style="margin-left: 25px;" :dark="dark" :sort-by.sync="sortBy" :hover="hover" :striped="striped"  :bordered="bordered" :small="small" :fixed="fixed" responsive="lg" :items="innerGridData" :fields="innerTableFields" :current-page="currentPage" :per-page="selectedperPage">
					</b-table>
				</template>
		</b-table>
		<div class="pt-1 pb-4" v-if="storeGridData.length == 0">
		<b-button style="background-color: #e4e5e6" class="w-100">{{$t('No Records Found.')}}</b-button>
		</div>
		<nav>
		<!-- <b-pagination class="pull-right" :total-rows="totalStoreRows" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/> -->
		 <b-button type="button" v-on:click="$refs.StoreDetailsModel.hide()" class="float-right ml-2 "><i class="fa fa-close"></i> {{$t('Cancel')}}</b-button>
		</nav>
      
    </b-modal>

	</b-card>
</template>

<script>
import VueNotifications from 'vue-notifications'
import * as toutPaieService from "./ToutPaieService.js";

export default {
	props: {
		caption: {
		type: String,
		default: 'Managers'
		},
		hover: {
		type: Boolean,
		default: true
		},
		striped: {
		type: Boolean,
		default: true
		},
		bordered: {
		type: Boolean,
		default: false
		},
		small: {
		type: Boolean,
		default: false
		},
		fixed: {
		type: Boolean,
		default: false
		},
		dark: {
		type: Boolean,
		default: false
		}
	},
	data: () => {
		return {
		sortBy : 'OrderNo',
		pageOptions: [5, 10, 15],
		currentPage: 1,
		perPage:30,
		selectedperPage:10,
		file: null,
		images: [1,2,3,4],
		shownData: [],
		shownData2: [],
		get_status:{},
		filters: {
                OrderNo: '',
                OrderDate: ''
              },
		orderStatus:null,
		orderStatusList:[],
		store_orderList:[
				{	StoreID:null,
					StoreName:null,
					OrderID:null,
				OrderItemID:null,	
			   	Items:[]
			}
		],
		store_orderList1:[
				{	StoreID:null,
					StoreName:null,
					OrderID:null,
				OrderItemID:null,	
			   	Items:[]
			}
		],
		strList:[
				{	StoreID:null,
					StoreName:null,
					OrderID:null,
					Status:null,
					UserID:null,
				OrderItemID:null,	
                OrderDeliveryAddress:null,
			   	Items:[]
			}
		],
		strList1:[
				{	StoreID:null,
					StoreName:null,
					OrderID:null,
					Status:null,
					UserID:null,
				OrderItemID:null,	
				OrderDeliveryAddress:null,
			   	Items:[]
			}
		],
		strAddress:[
				{	StoreID:null,
					StoreName:null,
					OrderID:null,
					Status:null,
					UserID:null,
				OrderItemID:null,	
				OrderDeliveryAddress:null,
			   	Items:[]
			}
		],
		orderList: [],
		showLoader:0,
		addressTableData:[],
		order_Details_field:{},
		orderItemsList:[],
		innerTableData: '',
	
		}
	},
	computed: {
		fields () {
		return [
			// {key: 'show_details',label: this.translateString(''), thStyle: {'width': '115px !important', 'min-width': '115px !important'}},
			{key: 'index',label: this.translateString('No'),  sortable: true},
			{key: 'OrderNo',label: this.translateString('Order_ID'),  sortable: true},
			{key: 'OrderDate',label: this.translateString('Order_Date'),  sortable: true},
			{key: 'TotalAmount',label: this.translateString('Total_Amount'),  sortable: true},
			{key: 'PaymentStatus',label: this.translateString('Payment_Status'),  sortable: true},
			// {key: 'StatusValue', label: this.translateString('Status'), sortable:true},
			{key: 'actions' , label: this.translateString('Action'), sortable:false, thStyle: {'width': '80px !important', 'min-width': '80px !important'}}
		]
		},
		gridData2: function() {
			return this.orderList;
		// Array.isArray(this.managersList) ? this.managersList : []
		},

		gridData () {
      const filtered = this.orderList.filter(item => {
        return Object.keys(this.filters).every(key =>
            // String(item[key]).includes(this.filters[key]))
			 String(item[key]).toLowerCase().includes( String(this.filters[key]).toLowerCase()))
      })
      return filtered.length > 0 ? filtered : [{
        FirstName: '',
      LastName: ''
      }]
    },
		totalRows: function () {
			return this.getRowCount();
		},
		captions: function() {
			return this.fields
		},
		

        innerStorefields () {
		return [
			{key: 'show_details',label: this.translateString(''), thStyle: {'width': '115px !important', 'min-width': '115px !important'}},
			// {key: 'OrderNo',label: this.translateString('OrderID'),  sortable: true},
			// {key: 'StoreID', label: this.translateString('Store_ID'), sortable:true},
			{key: 'StoreName', label: this.translateString('Store_Name'), sortable:true},
			{key: 'DeliveryMode', label: this.translateString('Delivery_Mode'), sortable:true},
			{key: 'status' , label: this.translateString('Status'), sortable:false, thStyle: {'width': '200px !important', 'min-width': '200px !important'}},
			{key: 'actions' , label: this.translateString('Action'), sortable:false, thStyle: {'width': '80px !important', 'min-width': '80px !important'}}
		]
		},
		storeGridData: function() {
			//console.log(this.strList);
			return this.strList;
		},
		totalStoreRows: function () {
			return this.getRowStoreCount()
		},
		storecaptions: function() {
			return this.innerStorefields
		},




		innerFields () {
			return [
				{key: 'ProductName',label: this.translateString('Product_Name'),  sortable: true,  thStyle: {'margin-left': '10px'}},
				// {key: 'OrderDeliveryAddress', label: this.translateString('OrderDeliveryAddress'), sortable:true},
				// {key: 'StoreName', label: this.translateString('Store_Name'), sortable:true},
				{key: 'Quantity',label: this.translateString('Quantity'),  sortable: true},
				{key: 'Amount',label: this.translateString('Amount'),  sortable: true},
		]
		},
		innerGridData: function() {
			return this.innerTableData;
		},
		innerTableFields: function() {
			return this.innerFields
		},

			addressFields() {
			return [
			    {key: 'OrderNo', label: this.translateString('Order_ID'), sortable:true},
				{key: 'OrderDeliveryAddress', label: this.translateString('Order_Delivery_Address'), sortable:true},
				{key: 'PaymentStatus', label: this.translateString('Payment_Status'), sortable:true}
					 
		]
		},
		addressGridData: function() {
			return this.addressTableData;
		},
		addressTableFields: function() {
			return this.addressFields
		},
	},
	methods: {
		translateString : function(colName) {
			return this.$i18n.t(colName);
		},
		getRowCount: function () {
			return this.orderList.length ;  
		},
		getRowStoreCount: function () {
			return 20 ;  
		},
		selectObject: function(Items){
			console.log("selected Order Items :",Items);
	  
		 this.addressTableData[0]=Items;
		 this.order_Details_field=Items;
		this.orderItemsList=Items.orderItems;
          
		this.getStoreList(Items);

		},

		toggleData(index){
			this.strList.forEach((data, ind) => {
				if (ind !== index && data._showDetails) {
					data._showDetails = false;
				} else if (ind === index) {
					this.innerTableData = data.Items;
				}
			});
		},

		selectedAddressData(index){
			this.strList.forEach((data, ind) => {
				if (ind !== index && data._showDetails) {
					data._showDetails = false;
				} else if (ind === index) {
					this.innerTableData = data.Items;
				}
			});
		},

		deleteItem: function (id) {
			console.log('in delete function');
		},
        UpdateStatus: function(order){
			//this.showLoader=true;
		//    console.log("order",order);
		   this.getUpdateOrderStatus(order);
		},
		updateItem: function(manager){
			console.log('in update function');
		},
		getStoreOrderList:function(orderList){
			let temp=[];

			if(orderList.length>0){
   

               for(let i=0;i<orderList.length;i++){
				       if(orderList[i].orderItems.length>0){
                      for(let j=0;j<orderList[i].orderItems.length;j++){
						  if(!temp.includes(orderList[i].orderItems[j].StoreID)){
                               temp.push(orderList[i].orderItems[j].StoreID);
						  }
					  } 
					}  
			   }
			   

                   //  console.log("temp",temp);
            for(var k=0;k<temp.length;k++){

				

				if(k>0){

					this.store_orderList1=[
				{	StoreID:null,
					StoreName:null,
					DeliveryMode:null,
					OrderID:null,
					OrderItemID:null,	
							Items:[]
						}
					];
				
					  this.store_orderList.push(this.store_orderList1[0]);
					  }
		       for(var i=0;i<orderList.length;i++){
						 
						 
				    
				        for(var j=0;j<orderList[i].orderItems.length;j++){
							var t1=temp[k];
							var t2=orderList[i].orderItems[j].StoreID;
						       
						  if(t1==t2){
							// 	console.log("orderList",(temp[k]==orderList[i].orderItems[j].StoreID));
							// 	console.log("orderList",orderList[i]);
							// 	 console.log("this.store_orderList",this.store_orderList);	
							//  console.log(temp[k]+" equals "+orderList[i].orderItems[j].StoreID+" k="+k);

						   this.store_orderList[k].StoreID=orderList[i].orderItems[j].StoreID;
						   this.store_orderList[k].StoreName=orderList[i].orderItems[j].StoreName;
						   this.store_orderList[k].OrderID=orderList[i].orderItems[j].OrderID;
						   this.store_orderList[k].DeliveryMode=orderList[i].orderItems[j].DeliveryMode;
						   this.store_orderList[k].OrderItemID=orderList[i].orderItems[j].OrderItemID;
						   this.store_orderList[k].Items.push(orderList[i].orderItems[j]);
						  }
				    	}
			   }
			 }
			 
			 

           

		//    console.log("this.store_orderList",this.store_orderList);		
		      }
		},
		changeOrderStatus:function(order,statusObj){
				//console.log(statusObj);
				statusObj.status = order;
					//  for(var i=0;i<this.strList.length;i++){
					// 	 if(this.strList[i].StoreID==statusObj.item.StoreID){
					// 	this.strList[i].status=statusObj.Status;
					// 	 }
					//  }
		},

		getStoreList:function(arrorderList){
 
         //   console.log(arrorderList);
			 let orderList=arrorderList.orderItems;
			let temp=[];
                

          for(var a=0;a<orderList.length;a++){
					   
				for(var b=0;b<this.orderStatusList.length;b++){

					if(orderList[a].StatusValue==this.orderStatusList[b].StatusValue){
						   
						   orderList[a]['Status']=this.orderStatusList[b].StatusId;

		            	}
				}

		  }

			

			if(orderList.length>0){
   

               for(var i=0;i<orderList.length;i++){
				             
						  if(!temp.includes(orderList[i].StoreID)){
                               temp.push(orderList[i].StoreID);
						  }
	 
			   }
			   

                   //  console.log("temp",temp);
            for(var k=0;k<temp.length;k++){

				

				if(k>0){

					this.strList1=[
				{	StoreID:null,
					StoreName:null,
					OrderDeliveryAddress:null,
					DeliveryMode :null,
					OrderID:null,
					Status:null,
					UserID:null,
					OrderItemID:null,	
					Items:[]
						}
					];
				
					  this.strList.push(this.strList1[0]);
					  }
		       for(var i=0;i<orderList.length;i++){
						 
							var t1=temp[k];
							var t2=orderList[i].StoreID;
						       
						  if(t1==t2){
						   //orderList[i]['Status'] = parseInt(arrorderList.Status);
						   this.strList[k].StoreID=orderList[i].StoreID;
						   this.strList[k].StoreName=orderList[i].StoreName;
						   this.strList[k].OrderDeliveryAddress=orderList[i].OrderDeliveryAddress;
						   this.strList[k].OrderID=orderList[i].OrderID;
						   this.strList[k].UserID=arrorderList.UserID;
						   this.strList[k].Status=parseInt(orderList[i].Status);
						   this.strList[k].DeliveryMode=(orderList[i].DeliveryMode=="H"?"At Delivery Address":"At Store");
						   this.strList[k].OrderItemID=orderList[i].OrderItemID;
						   this.strList[k].Items.push(orderList[i]);
						   
						  }
				  
			   }
			 }
			
			 
		//    console.log("this.strList",this.strList);		 
		     }
		},

		saveItem: function(){
			// console.log('in save itom function');
		},
			clearObjects:function(){

			//    console.log("clear Object");
			   
				this.strList1=[
				{	StoreID:null,
					StoreName:null,
					OrderDeliveryAddress:null,
					OrderID:null,
					DeliveryMode:null,
					OrderItemID:null,	
					Items:[]
						}
					];
					this.strList=[
				{	StoreID:null,
					StoreName:null,
					OrderDeliveryAddress:null,
					DeliveryMode:null,
					OrderID:null,
					OrderItemID:null,	
					Items:[]
						}
					];

                    this.addressTableData=[
				    {	
					StoreID:null,
					StoreName:null,
					OrderDeliveryAddress:null,
					DeliveryMode:null,
					OrderID:null,
					OrderItemID:null,	
					Items:[]
						}
					];
				   this.order_Details_field={};
		

		},

		getOrderStatus: function() {
			toutPaieService.getData('order/master/status').then(res => {
				this.orderStatusList = res.data;
			//	console.log("this.orderStatusList :",this.orderStatusList);
			});
		},

		getUpdateOrderStatus: function(order) {
			 
			 let url=null;

			  var form = new FormData();
			   form.append("Status", order.Status);
			   form.append("UserID", order.UserID);
	  
			 url="order/store/status/update/"+order.StoreID+"/"+order.OrderID;

			  toutPaieService.saveData(form, url).then(data => {
					let msg=data.data[0].message;
						if (data.status == 200 && msg=="Success") { 
							// this.clearObjects();
							this.showLoader=0;
							this.getOrders();
						VueNotifications.success({message: this.translateString("Store_Status_Updated_Successfully_Saved"),timeout:2500});
						}else{
							this.showLoader=0;
						VueNotifications.error({message:this.translateString(msg),timeout:2500});
						}   
						
					});
		},

		getOrders: function() {
			
		this.loginUser=JSON.parse(window.localStorage.getItem("loginUser"));
		//    console.log("",this.loginUser);	
		   
		     let url =null;
				if(this.loginUser.Role=="M"){
					url = 'orders/user/store/'+this.loginUser.Users[0].StoreID;
				}else{
								url="orders";
				}

				// console.log("url---->",url);

			toutPaieService.getData(url).then(res => {
				this.orderList = res.data;

				 for(var i=0;i<this.orderList.length;i++)
          {
            this.orderList[i].PaymentStatus=(this.orderList[i].PaymentStatus=="Y"?"Done":"Pending");
		  }     
		        this.getStoreOrderList(this.orderList);
			//	console.log("this.orderList",this.orderList);
			});

			
		}
	},

	beforeMount(){
		this.getOrders();
		this.getOrderStatus();
		  //  console.log("temp");
	}
}
</script>